div.cropped.image {
  overflow: hidden;
  width: calc(100% - 2em);
  height: calc(12em * 2/3);
  margin: 0;
}

div.card {
  margin: 0.5em;
  padding: 0.5em;

  a {
    text-decoration: none;
  }
  h4 {
    margin: 0;
    padding: 0 0 0.25em 0;
  }

  div.cropped.image {
    overflow: hidden;
    width: 100%;
    height: calc(12em * 2/3);
  }
  img {
    width: 100%;
    padding: 0;
  }

}

p.note {
  margin: 0 0.5em !important;
  padding: 0 0.5em !important;
  font-style: italic;
  color: $note-color;
}
